/**
 *
 * Input
 *
 */

import React, { useState, useEffect } from 'react'
import Select from 'react-select'
import { selectThemeColors } from '@utils/Utils'
import { useForm } from 'react-hook-form'
import { FormGroup, FormText, Input, Label } from 'reactstrap'

const ReactSelectGroup = props => {
  const {
    error,
    disabled,
    placeholder,
    label,
    name,
    defaultValue = '',
    onInputChange,
    required = false,
    autoComplete = 'off',
    options = [],
    ...rest
  } = props

  const [value, setValue] = useState('')
  const [initValue, setInitValue] = useState(false)
  const { register } = useForm()

  const _onChange = (value, { action, removedValue }) => {
    onInputChange(name, value)
    setInitValue(true)
    setValue(value)
  }

  return (
    <FormGroup>
      {label && <Label className='form-label'>
        {label} {required && <span className='text-danger'>*</span>}
      </Label>}
      <Select
        theme={selectThemeColors}
        className='react-select'
        classNamePrefix='select'
        defaultValue={defaultValue}
        options={options}
        isDisabled={disabled}
        name={name}
        isClearable={false}
        onChange={_onChange}
        {...rest}
      />
      <FormText color="danger">{error && error[0]}</FormText>
    </FormGroup>
  )
}

export default ReactSelectGroup
