// ** React Imports
import React, { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import classnames from "classnames"

// ** Custom Components
import InputGroup from '@components/input-group'
import ReactSelectGroup from '@components/react-select-group'

// ** Third Party Components
import { Button, Col, Form, Row } from 'reactstrap'

// ** Store Actions
import { getAllUsers } from '@store/Users/actions'
import { getAllUserCars } from '@store/UserCars/actions'
import { getAllAgents, getAllEmployees } from '@store/Employees/actions'
import { getAllVendors, getAllVendorEmployees } from '@store/Vendors/actions'
import { getAllAutoServiceCategories } from '@store/AutoServiceCategory/actions'
import { autoServiceEditChange, updateAutoService } from '@store/AutoServiceRequest/actions'
import UILoader from "../../../@core/components/ui-loader"

const AutoServiceEdit = ({
  selectedData
}) => {
  const dispatch = useDispatch(),
    { allData: allUsers } = useSelector(state => state.users),
    { allData: allUserCars } = useSelector(state => state.userCars),
    { allData: allEmployees, agents } = useSelector(state => state.employees),
    { allData: allVendors, employeeAllData: allVendorEmployees } = useSelector(state => state.vendors),
    { allData: allAutoServiceCategories } = useSelector(state => state.autoServiceCategories),
    { isLoading, isSubmitting, formErrors } = useSelector(state => state.autoServices)

  useEffect(() => {
    dispatch(getAllUsers())
    dispatch(getAllEmployees())
    dispatch(getAllAgents())
    dispatch(getAllVendors())
    dispatch(getAllAutoServiceCategories())
  }, [dispatch])

  // ** Get user cars on changing user
  useEffect(() => {
    if (selectedData.userId) {
      dispatch(getAllUserCars({ userId: selectedData.userId }))
    }
  }, [selectedData.userId])

  // ** Get vendor employee on changing vendor
  useEffect(() => {
    if (selectedData.vendorId) {
      dispatch(getAllVendorEmployees(selectedData.vendorId))
    }
  }, [selectedData.vendorId])

  // ** Function to handle form submit
  const handleSubmit = event => {
    event.preventDefault()
    dispatch(updateAutoService(selectedData._id))
  }

  const handleFormChange = (name, value, type = 'text') => {
    dispatch(autoServiceEditChange(name, value, type))
  }

  const userOptions = allUsers.map((userItem) => {
    return { value: userItem._id, label: `${userItem.firstName} ${userItem.lastName}` }
  })
  const { userInfo } = selectedData
  let userOption = null
  if (selectedData.userId && userInfo.length > 0) {
    userOption = { value: selectedData.userId, label: `${userInfo[0].firstName} ${userInfo[0].lastName}` }
  }

  const userCarOptions = allUserCars.map((userCarItem) => {
    return { value: userCarItem._id, label: `${userCarItem.carMake} ${userCarItem.carModel} ${userCarItem.engineCapacity}` }
  })
  const { userCarInfo } = selectedData
  let userCarOption = null
  if (selectedData.userCarId && userCarInfo.length > 0) {
    userCarOption = { value: selectedData.userCarId, label: `${userCarInfo[0].carMake} ${userCarInfo[0].carModel} ${userCarInfo[0].engineCapacity}` }
  }

  const employeeOptions = allEmployees.map((employeeItem) => {
    return { value: employeeItem._id, label: `${employeeItem.firstName} ${employeeItem.lastName}` }
  })
  const { employeeInfo } = selectedData
  let employeeOption = null
  if (selectedData.carhootEmployeeId && employeeInfo.length > 0) {
    employeeOption = { value: selectedData.carhootEmployeeId, label: `${employeeInfo[0].firstName} ${employeeInfo[0].lastName}` }
  }
  const vendorOptions = allVendors.map((vendorItem) => {
    return { value: vendorItem._id, label: `${vendorItem.vendorName}` }
  })

  const { vendorInfo } = selectedData
  let vendorOption = null
  if (selectedData.vendorId && vendorInfo.length > 0) {
    vendorOption = { value: selectedData.vendorId, label: `${vendorInfo[0].vendorName}` }
  }

  const vendorEmployeeOptions = allVendorEmployees.map((vendorEmployeeItem) => {
    return { value: vendorEmployeeItem._id, label: `${vendorEmployeeItem.firstName} ${vendorEmployeeItem.lastName}` }
  })

  const { vendorEmployeeInfo } = selectedData
  let vendorEmployeeOption = null
  if (selectedData.vendorId && vendorEmployeeInfo.length > 0) {
    vendorEmployeeOption = { value: selectedData.vendorId, label: `${vendorEmployeeInfo[0].firstName} ${vendorEmployeeInfo[0].lastName}` }
  }

  const fieldAgentOptions = agents.map((agent) => {
    return { value: agent._id, label: `${agent.firstName} ${agent.lastName}` }
  })

  const { fieldAgentInfo } = selectedData
  let fieldAgentOption = null
  if (selectedData.fieldAgentId && fieldAgentInfo.length > 0) {
    fieldAgentOption = { value: selectedData.fieldAgentId, label: `${fieldAgentInfo[0].firstName} ${fieldAgentInfo[0].lastName}` }
  }

  const categoryOptions = allAutoServiceCategories.map((category) => {
    return { value: category._id, label: `${category.serviceName}` }
  })

  const { categoryInfo } = selectedData
  let categoryOption = null
  if (selectedData.orderCategoryId && categoryInfo.length > 0) {
    categoryOption = { value: selectedData.orderCategoryId, label: `${categoryInfo[0].serviceName}` }
  }

  return (
    <Row>
      <Col sm='12'>
        <Form onSubmit={handleSubmit}>
          <Row>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'User'}
                error={formErrors['userId']}
                name={'userId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={userOption}
                options={userOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['userId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'User Car'}
                error={formErrors['userCarId']}
                name={'userCarId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={userCarOption}
                options={userCarOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['userCarId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'Carhoot Employee'}
                error={formErrors['carhootEmployeeId']}
                name={'carhootEmployeeId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={employeeOption}
                options={employeeOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['carhootEmployeeId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'Field Agent'}
                error={formErrors['fieldAgentId']}
                name={'fieldAgentId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={fieldAgentOption}
                options={fieldAgentOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['fieldAgentId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'Vendor'}
                error={formErrors['vendorId']}
                name={'vendorId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={vendorOption}
                options={vendorOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['vendorId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'Vendor Employee'}
                error={formErrors['vendorEmployeeId']}
                name={'vendorEmployeeId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={vendorEmployeeOption}
                options={vendorEmployeeOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['vendorEmployeeId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <ReactSelectGroup
                label={'Category'}
                error={formErrors['orderCategoryId']}
                name={'orderCategoryId'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value, 'option')
                }}
                defaultValue={categoryOption}
                options={categoryOptions}
                required={true}
                className={classnames({ 'is-invalid': formErrors['orderCategoryId'] })}
              />
            </Col>
            <Col md='4' sm='12'>
              <InputGroup
                type={'number'}
                label={'Order Price'}
                error={formErrors['orderPrice']}
                name={'orderPrice'}
                placeholder={'Order Price'}
                onInputChange={(name, value) => {
                  handleFormChange(name, value)
                }}
                defaultValue={selectedData.orderPrice}
                required={true}
                className={classnames({ 'is-invalid': formErrors['orderPrice'] })}
              />
            </Col>
            <Col className='d-flex flex-sm-row flex-column mt-2' sm='12'>
              <UILoader blocking={isLoading} className='mr-sm-1'>
                <Button.Ripple className='mb-1 mb-sm-0 mr-0' type='submit' color='primary'>
                  Save Changes
                </Button.Ripple>
              </UILoader>
              <Button.Ripple color='secondary' outline>
                Reset
              </Button.Ripple>
            </Col>
          </Row>
        </Form>
      </Col>
    </Row>
  )
}

export default AutoServiceEdit
