// ** React Imports
import { useEffect, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { Link, useParams } from 'react-router-dom'
import { User } from "react-feather"

// ** AutoService Edit Components
import AccountTab from './EditForm'

// ** Store & Actions
import { getAutoService } from '@store/AutoServiceRequest/actions'

// ** Third Party Components
import { Alert, Card, CardBody, Col, Nav, NavItem, NavLink, Row, TabContent, TabPane } from 'reactstrap'

// ** Custom Components
import UILoader from '@components/ui-loader'

// ** Styles
import '@styles/react/apps/app-users.scss'


const AutoServiceEdit = () => {
  // ** States & Vars
  const [activeTab, setActiveTab] = useState('1'),
    dispatch = useDispatch(),
    { isLoading, selectedData } = useSelector(state => state.autoServices),
    { id } = useParams()

  // ** Function to toggle tabs
  const toggle = tab => setActiveTab(tab)

  // ** Function to get autoService on mount
  useEffect(() => {
    dispatch(getAutoService(id))
  }, [dispatch])

  return selectedData !== null && selectedData !== undefined ? (
    <UILoader blocking={isLoading}>
      <Row className='app-user-edit'>
        <Col sm='12'>
          <Card>
            <CardBody className='pt-2'>
              <Nav pills>
                <NavItem>
                  <NavLink active={activeTab === '1'} onClick={() => toggle('1')}>
                    <User size={14} />
                    <span className='align-middle d-none d-sm-block'>Account</span>
                  </NavLink>
                </NavItem>
              </Nav>
              <TabContent activeTab={activeTab}>
                <TabPane tabId='1'>
                  <AccountTab selectedData={selectedData} />
                </TabPane>
              </TabContent>

            </CardBody>
          </Card>
        </Col>
      </Row>
    </UILoader>
  ) : (
    <Alert color='danger'>
      <h4 className='alert-heading'>AutoService not found</h4>
      <div className='alert-body'>
        AutoService with id: {id} doesn't exist. Check list of all AutoServices: <Link to='/autoServiceRequest/list'>AutoService List</Link>
      </div>
    </Alert>
  )
}

export default AutoServiceEdit
